import commonIndex from './index.common'

// Plugins
import basePlugins from './basePlugins'
import auth from './auth/cpaas'
import webrtc from './webrtc'
import call from './call/cpaas'
import connectivity from './connectivity/cpaas'
import messaging from './messaging/cpaas'
import notifications from './notifications/cpaas'
import presence from './presence/cpaas'
import groups from './groups/cpaas'
import subscription from './subscription/cpaas'
import createCodecRemover from '../../fcs/src/js/sdp/codecRemover'
import users from './users/cpaas'
import './docs/docs'

const defaultPlugins = [
  ...basePlugins,
  { name: 'authentication', fn: auth },
  { name: 'webrtc', fn: webrtc },
  { name: 'call', fn: call },
  { name: 'connectivity', fn: connectivity },
  { name: 'messaging', fn: messaging },
  { name: 'notifications', fn: notifications },
  { name: 'presence', fn: presence },
  { name: 'groups', fn: groups },
  { name: 'subscription', fn: subscription },
  { name: 'users', fn: users }
]

function root (options = {}, plugins = []) {
  return commonIndex(options, [...defaultPlugins, ...plugins])
}

// Alias 'create' to be equal to the root function
root.create = root

root.sdpHandlers = {
  createCodecRemover
}

// Export this way as a work-around, so it can be used as `<export>();`.
// See: https://github.com/webpack/webpack/issues/706
module.exports = root
